.footer {
  position: relative;
  width: 100%;
  height: max-content;
  background-color: #000000;
  padding: 45px 0 45px;

  @media screen and (max-width: 560px) {
    padding: 49px 0 0;
  }

  &__title {
    position: relative;
    color: #ffffff;
    font-family: Formular;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    font-size: 21px;
    letter-spacing: 4.38px;
    text-transform: uppercase;
  }

  &__menu {
    width: 100%;
    height: max-content;
    margin-top: 50px;

    @media screen and (max-width: 560px) {
      display: none;
    }

    .footer__nav {
      margin-bottom: 0;
      justify-content: space-between;
      
    }
  }

  &__content {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
    height: max-content;

    @media screen and (max-width: 999px) {
      flex-direction: column;
      align-items: center;
    }

    &-imageFace {
      width: 315px;
      height: 242px;
      
      @media screen and (max-width: 999px) {
        width: 230px;
      }

      & > img {
        position: relative;
        left: -70px;
        top: 50px;
        width: 100%;
        height: 100%;
        object-fit: contain;

        @media screen and (max-width: 999px) {
          position: static;
          left: 0;
          top: 0;
        }
      }
    }

    &-left {
      width: auto;
      display: flex;
      justify-content: flex-start;
      margin-right: 50px;
      height: 305px;

      @media screen and (max-width: 999px) {
        justify-content: center;
        align-items: flex-end;
      }

      @media screen and (max-width: 560px) {
        align-items: flex-start;
        width: 100%;
        margin-right: 0;
        height: max-content;
      }
    }

    &-right {
      margin-top: 75px;
      margin-right: 37px;
      width: 480px;
      max-width: 590px;

      @media screen and (max-width: 999px) {
        width: 100%;
        margin-right: 0;
      }

      @media screen and (max-width: 560px) {
        flex-direction: column;
      }

      &-info {
        position: relative;
        display: flex;

        @media screen and (max-width: 999px) {
          justify-content: center;
        }

        @media screen and (max-width: 560px) {
          flex-direction: column;
        }
      }
    }

    &-mobimage {
      display: none;
      width: 125px; 
      height: 125px; 

      img { 
        width: 100%; 
        height: 100%;
      }
    }

    &-name {
      align-self: flex-end;
      width: 150px;
      min-width: 150px;
      flex-wrap: wrap;
      color: #fff;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: 0.3px;
      color: #ffffff;
      font-family: Formular;
      font-size: 16px;
      box-sizing: border-box;
    }

    &-contacts, &-sendmail {
      justify-content: center;
      display: flex;
      flex-direction: column;
      color: #fff;
      font-family: ProximaRegular, sans-serif;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.71;
      letter-spacing: normal;

      & > span {
        font-weight: bold;
      }
    }

    &-contacts {
      width: 150px;
      max-width: 150px;
      align-self: flex-start;
      margin-right: 73px;

      .second-span {
        margin-top: 23px; 
      }

      & > a {
        color: #fff;
        font-family: ProximaRegular, sans-serif;
        font-size: 14px;
        font-weight: normal;
        line-height: 24px;
      }
    }

    &-sendmail {
      height: max-content;

      p {
        margin: 0;
        white-space: pre-line;
      }
    }
  }

  &__social {
    margin-top: 20px;

    .social__footer {
      margin: 0;

      a {
        transition: all .4s ease;
      }

      a:hover {
        color: #fff; 
        opacity: .7
      }
    }
  }
}

@media screen and (max-width: 560px) {
  .footer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &__nav {
      display: none;
    }

    &__title {
      font-size: 14px;
    }

    &__content {

      &-imageFace {
        display: none;
      }

      &-mobimage {
        display: block;
        margin: 21px 0 41px;
      }

      &-image {
        display: none;
      }

      &-name {
        align-self: flex-start;
      }

      &-contacts {
        margin: 0 0 39px 0;
      }

      &-right, &-left {
        flex-direction: column;
      }

      &-right {
        margin-top: 35px;
      }
    }

    &__social {
      display: block;
    }
  }
}